    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 78vh;
    width: 100%;
    padding: 80px;
}

.login {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    height: 280px;
}

input {
    width: 300px;
    height: 40px;
    margin: 20px;
    padding: 15px 30px 15px 30px;
    font-size: 24px;
    border-radius: 1vh;
    border: 1px solid indigo;
}
input {
    outline: none;
}
.sendform {
    margin-top: 40px;
    background-color: indigo; /* Green */
    border: none;
    border-radius: 0.3rem;
    color: white;
    font-weight: bold;
    font-size: 24px;
    padding: 1rem 2.5em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
}

.sasslogo {
    -webkit-align-self: center;
            align-self: center;
    text-align: center;
    color: #ffffff;
    height: 150px;
    width: 150px;
    margin-top: 70px;
}
.main {
    overflow-x: hidden;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
}

h2 {
    color: white;
}

a {
    text-decoration: none;    
}

.banner {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #000000;
    width: 100%;
    height: 10vh;
    transition: 1s ease;
}

/* .fixed { 
    position: absolute;
    bottom: 0;
} */

.endpointsDiv {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    background-color: #e3e3e3;
    transition: 1s ease;
    height: 0vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.cmdDiv {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #e3e3e3;
    transition: 0.7s ease;
    height: 0vh;
    overflow-y: hidden;
    overflow-x: hidden;
}

.outputDiv {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #e3e3e3;
    transition: 0.7s ease;
    height: 0vh;
    overflow-y: auto;
    overflow-x: hidden;
}

#openEndpointsDiv {
    height: auto !important;
}

#openCommandsDiv{
    height: 60vh !important;
}

#openOutputDiv{
    height: 60vh !important;
}

.container {
    display: grid;
    grid-template-columns: 4;
    grid-template-rows: 4;
    margin: 10px 0px 10px 40px;
    width: 100%;
}

.itemSelect {
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 1;
    grid-row-end: 1;
    padding-left: 6px;
}

.itemStatus {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 1;
}

.itemEndpointID{
    grid-column-start: 3;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 1;
}

.itemHostname {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 1;
}

.tableSeparator {
    padding-left: 15px;
}

.cell {
    display: block;
    box-sizing: content-box;
    float: none;
    line-height: normal;
    position: static;
    z-index: auto;
    margin-top: 1vh;
    margin-bottom: 1vh;
}

.mainCmd{
    -webkit-align-self: center;
            align-self: center;
    background-color: white;
    margin-top: 1vh;
    margin-bottom: 1vh;
}

.textboxCmd {
    padding: 10px;
    border-radius: 0.3rem;
    padding-left: 25px;
    height: 20px;
    width: 85%;
    font-size: 20px;
    font-weight: 600;
    
}

.buttonCmd {
    margin-top: 6vh;
    background-color: indigo; /* Green */
    border: none;
    border-radius: 0.3rem;
    color: white;
    padding: 0.6rem 1em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}

.scriptUploadBtn{
    margin-bottom: 1rem;
    margin-top: 1rem;
}

label {
    background-color: indigo;
    color: white;
    padding: 0.5rem;
    font-family: sans-serif;
    border-radius: 0.3rem;
    cursor: pointer;
    margin-top: 1rem;
  }
  
#file-chosen{
    margin-right: 0.3rem;
}

.status-dot{
    height: 16pxs; width: 16px; padding-left: 17px;
}

.botList{
    display: -webkit-flex;
    display: flex;
    height: 100px;
    overflow-x: auto;
    overflow-y: hidden;
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 10px mediumpurple;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: indigo;
    border-radius: 10px;
}

.botsListed {
    font-weight: bold;
    font-size: 18px;
    min-width: 33%;
    height: 100%;
    line-height: 92px;
    text-align: center;
}

.botsListed:target {
    border-right: 2px solid black;
    border-left: 2px solid black
}

.botOutput{
    display: -webkit-flex;
    display: flex;
    padding: 20px;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.endpRow{
    display: none;
    background: #e3e3e3;
    height: 0;
    transition: 1s ease;
}
.openEndpRow{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    background: #e3e3e3;
    height: auto;
    transition: 1s ease;
}
@-webkit-keyframes slideInFromLeft{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@keyframes slideInFromLeft{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
.mainColumn{
    background: rgba(0, 0, 0, 0.8);
    position: absolute;
    width: 100%;
    height: 100%;
}
.Column{
    width: 500px;
    transition: width 2s;
    height: 100%;
    background: #161616;
    -webkit-animation: 0.2s ease-in-out 1 slideInFromLeft;
            animation: 0.2s ease-in-out 1 slideInFromLeft;
    display: grid;
    grid-template-rows: 15vh 70vh 15vh;
}
.colheader{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.colheader > img{
    width: 90px;
    height: 90px;
}
.colfooter{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.colfooter > p {
    color: #5B666E;
    font-size: 50px;
}
.colbody{
    display: grid;
    grid-template-rows: 10vh 30vh 20vh 10vh;
}
.colOptions{
    grid-row-start: 2;
    grid-row-end: 3;
}
.colLogout{
    grid-row-start: 4;
    grid-row-end: 5;
}
.colOptions > div > div > img{
    width: 35px;
    height: 35px;
}
.colLogout > div > img{
    width: 35px;
    height: 35px;
}
.colOptions >  div > p{
    font-size: 30px;
}
.colLogout >  p{
    font-size: 30px;
}
.colmulticmd, .colhistory, .colsettings, .colLogout {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    color: white;
    cursor: pointer;
}
.colmulticmd > div, .colhistory > div, .colsettings > div, .colLogout > div {
    background: #EDEDED;
    border-radius: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 50px;
    width: 50px;
    margin-left: 50px;
    margin-right: 20px;
}
.colmulticmd:hover > div, .colmulticmd:hover > p, .colhistory:hover > div, .colhistory:hover > p, .colsettings:hover > div, .colsettings:hover > p, .colLogout:hover > div, .colLogout:hover > p {
    -webkit-transform: translate(50px);
            transform: translate(50px);
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.colmulticmd:hover > div, .colhistory:hover > div, .colsettings:hover > div, .colLogout:hover > div {
    background: black;
}
.colmulticmd:hover > div > img, .colhistory:hover > div > img, .colsettings:hover > div > img, .colLogout:hover > div > img {
    -webkit-filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(330deg) brightness(108%) contrast(101%);
            filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(330deg) brightness(108%) contrast(101%);
}
.colmulticmd:hover, .colhistory:hover, .colsettings:hover, .colLogout:hover {
    background: white !important;
    color: black !important;
}
